import Vue from 'vue';
import Router from 'vue-router';
import Starter from './pages/StarterPage.vue';
import Perfil from './pages/Perfil.vue';
import Fotografia from './pages/Fotografia.vue';
import Edicion from './pages/Edicion.vue';
import Video from './pages/Video.vue';
import Curriculum from './pages/Curriculum.vue';
import StarterNavbar from './layout/StarterNavbar.vue';
import StarterFooter from './layout/StarterFooter.vue';
import AOS from 'aos'

Vue.use(Router);  

export default new Router({
  linkExactActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'index',
      components: {
        default: Starter,
        header: StarterNavbar,
        footer: StarterFooter
      },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path:'/perfil',
      name: 'perfil',
      components:{
        default: Perfil,
        header: StarterNavbar,
        footer: StarterFooter
      },
      props: {
        header: { colorOnScroll:400},
        footer: { backgroundColor: 'black'}
      }
    },
    {
      path:'/Fotografia',
      name: 'fotografia',
      components:{
        default: Fotografia,
        header: StarterNavbar,
        footer: StarterFooter
      },
      props: {
        header: { colorOnScroll:400},
        footer: { backgroundColor: 'black'}
      }
    },
    {
      path:'/Edicion',
      name: 'edicion',
      components:{
        default: Edicion,
        header: StarterNavbar,
        footer: StarterFooter
      },
      props: {
        header: { colorOnScroll:400},
        footer: { backgroundColor: 'black'}
      }
    },
    {
      path:'/Video',
      name: 'video',
      components:{
        default: Video,
        header: StarterNavbar,
        footer: StarterFooter
      },
      props:{
        header:{ colorOnScroll:400},
        footer: { backgroundColor: 'black'}
      }
    },
    {
      path:'/Curriculum',
      name: 'curriculum',
      components:{
        default: Curriculum,
        header: StarterNavbar,
        footer: StarterFooter
      },
      props:{
        header:{ colorOnScroll:400},
        footer: { backgroundColor: 'black'}
      }
    },
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});
