<template>
    <div id="galeria" oncontextmenu="return false">
  <img class="image" v-for="(image, i) in images" :key="image" :src="image" @click="index = i">
  <gallery-slideshow :images="images" :index="index" @close="index = null"></gallery-slideshow>
</div>
</template>
<script>
import {GallerySlideshow} from '@/components';
export default {
    components: {
    GallerySlideshow
  },
  methods() {
      return{
          onClick(i) {
      this.index = i;
    },
          noClick(){
            oncontextmenu="return false";
          }
      }
    
  },
  data() {
      return{
          images: [
      'img/foto1.jpg',
      'img/foto2.jpg',
      'img/foto3.jpg',
      'img/foto4.jpg',
      'img/foto5.jpg',
      'img/foto6.jpg',
      'img/foto7.jpg',
      'img/foto8.jpg',
      'img/foto10.jpg',
      'img/foto9.jpg',
      'img/foto12.jpg',
      'img/foto11.jpg',
      

      
    ],
    index:null
      }
    
    }
}
</script>
<style lang="scss">
$bootstrap-sm: 576px;
$bootstrap-md: 768px;
$bootstrap-lg: 992px;
$bootstrap-xl: 1200px;
#galeria {
  img {
    height: 75vw;
    transition: transform .2s ease-in; 
     -webkit-user-select: none; 
      -webkit-touch-callout: none; 
      -moz-user-select: none; 
      -ms-user-select: none; 
      user-select: none;
    &:hover{
      transform: scale(1.3);
    }
    
